<template>
  <header>
    <div class="contenar">
      <div class="head">
        <div class="logo">
          <a href="http://morlacivert.com/">
            <img src="../../assets/images/logo.png" alt="logo" />
          </a>
        </div>
        <nav class="nav-hide" id="side">
          <div class="icon" id="icon" @click="navBarc">
            <span></span>
            <span></span>
            <span></span>
          </div>
          <ul id="side-bar" @click="sidec">
            <li>
              <router-link to="/" class="active">Home</router-link>
            </li>
            <li>
              <router-link to="/about">About</router-link>
            </li>
            <li>
              <a href="/#office-design">Office design</a>
            </li>
            <li>
              <a href="/#retail-interior-design">Retail Interior Design</a>
            </li>
            <li>
              <a href="#contact">Contact Us</a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "HeaderSection",
  data() {
    return {
      navBar: "",
      closeBar: "",
      sideBar: "",
      side: "",
    };
  },
  methods: {
    setvaribal() {
      this.navBar = this.$el.querySelector("#icon");
      this.closeBar = this.$el.querySelector("#closebar");
      this.sideBar = this.$el.querySelector("#side");
      this.side = this.$el.querySelector("#side-bar");
    },
    navBarc() {
      this.sideBar.classList.toggle("nav-show");
      if (this.sideBar.classList.contains("nav-show") === true) {
        this.$el.querySelector(".nav-show").style.cssText =
          "position: fixed; right: 0; top: 0;";
      } else {
        // this.sideBar.style.cssText = 'position: fixed; right: 20px; top: 100px;'
      }
    },
    sidec() {
      this.sideBar.classList.toggle("nav-show");
    },
  },
  mounted() {
    this.setvaribal();
  },
};
</script>

<style scoped>
