  font-weight: 500;
  font-size: 18px;
  position: relative;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}

header nav ul li .active {
  color: var(--main-color);
}

header nav ul li a::before {
  content: "";
  width: 100%;
  height: 3px;
  background: var(--main-color);
  position: absolute;
  left: -100%;
  top: 0;
  transition: 0.3s;
  -webkit-transition: 0.3s;
