import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

// import normaliz and fontawsome icon 
import "normalize-css"
/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { faUserSecret, faEnvelope, faAngleRight, faAngleLeft } from '@fortawesome/free-solid-svg-icons'

import { faTwitter, faFacebookF, faInstagram, faLinkedin, faWhatsapp } from '@fortawesome/free-brands-svg-icons'

/* add icons to the library */
library.add(faUserSecret, faTwitter, faFacebookF, faInstagram, faLinkedin, faEnvelope, faWhatsapp, faAngleRight, faAngleLeft)


createApp(App).use(store).use(router).component('font-awesome-icon', FontAwesomeIcon).mount("#app");
