<template>
  <section class="intro" id="home">
    <div class="slider">
      <img src="../../assets/images/intro-1.jpg" alt="intro-1" />
      <img src="../../assets/images/intro-2.jpg" alt="intro-2" />
      <img src="../../assets/images/intro-3.jpg" alt="intro-3" />
      <img src="../../assets/images/intro-4.jpg" alt="intro-4" />
      <img src="../../assets/images/intro-5.jpg" alt="intro-5" />
    </div>
    <ul>
      <li data-number="0"></li>
      <li data-number="1"></li>
      <li data-number="2"></li>
      <li data-number="3"></li>
      <li data-number="4"></li>
    </ul>
    <!-- <i class="fas fa-angle-right next fa-2x"></i> -->
    <font-awesome-icon
      icon="fa-solid fa-angle-right"
      class="right"
      @click="nextSlider"
    />
    <!-- <i class="fas fa-angle-left prev fa-2x"></i> -->
    <font-awesome-icon
      icon="fa-solid fa-angle-left"
      class="left"
      @click="prevSlider"
    />
  </section>
</template>

<script>
export default {
  name: "SliderSection",
  data() {
    return {
      sliderImage: [],
      sliderUl: [],
      nextButton: "",
      prevButton: "",
      startSlider: 0,
    };
  },
  methods: {
    setvaribal() {
      this.sliderImage = this.$el.querySelectorAll(".slider img");
      this.sliderUl = this.$el.querySelectorAll("ul li");
      this.nextButton = this.$el.querySelector(".right");
      this.prevButton = this.$el.querySelector(".left");
    },
    activeImage() {
      // add active
      this.sliderImage[this.startSlider].classList = "active";
      this.sliderUl[this.startSlider].classList = "active";
    },
    active(array, ul) {
      array.forEach((e) => {
        e.classList.remove("active");
      });
      ul.forEach((e) => {
        e.classList.remove("active");
      });
      array[this.startSlider].classList = "active";
      ul[this.startSlider].classList = "active";
    },
    nextSlider() {
      if (this.startSlider === 4) {
        this.startSlider = 0;
        this.active(this.sliderImage, this.sliderUl);
      } else {
        this.startSlider++;
        this.active(this.sliderImage, this.sliderUl);
      }
    },
    prevSlider() {
      if (this.startSlider === 0) {
        this.startSlider = 4;
        this.active(this.sliderImage, this.sliderUl);
      } else {
        this.startSlider--;
        this.active(this.sliderImage, this.sliderUl);
      }
    },
    sliderUi() {
      this.sliderUl.forEach((e) => {
        e.addEventListener("click", (el) => {
          this.startSlider = el.target.dataset.number;
          this.active(this.sliderImage, this.sliderUl);
        });
      });
    },
    sliderAnimation() {
      setInterval(() => {
        this.nextSlider();
      }, 7000);
    },
  },
  mounted() {
    this.setvaribal();
    this.activeImage();
    this.sliderUi();
    this.sliderAnimation();
  },
};
</script>

<style scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "League Spartan", sans-serif;
  --main-color: #2196f3;
  --trans-background: rgb(0 0 0 / 60%);
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

/* contenar */
.contenar {
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 768px) {
  .contenar {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .contenar {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .contenar {
    width: 1170px;
  }
}
/* end */

.intro {
  background: black;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.intro::before {
  content: "";
  background: var(--trans-background);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
}

.intro ul {
  list-style-type: none;
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 50%;
  z-index: 5;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
}

.intro ul li {
  width: 15px;
  height: 15px;
  margin: 0 5px;
  border: 1px solid rgb(8, 240, 244);
  background: none;
  cursor: pointer;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  transition: background 1s;
  -webkit-transition: background 1s;
  -moz-transition: background 1s;
  -ms-transition: background 1s;
  -o-transition: background 1s;
}

.intro ul li.active {
  background: rgb(8, 240, 244);
}

.intro .right {
  font-size: 25px;
  position: absolute;
  top: 50%;
  right: 30px;
  color: white;
  z-index: 3;
  cursor: pointer;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
}

.intro .left {
  font-size: 25px;
  position: absolute;
  top: 50%;
  left: 30px;
  color: white;
  cursor: pointer;
  z-index: 3;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
}

.intro .left:hover,
.intro .right:hover {
  color: var(--main-color);
}

.intro .slider {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.intro .slider img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  opacity: 0;
  transition: opacity 1s;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -ms-transition: opacity 1s;
  -o-transition: opacity 1s;
}

.intro .slider img.active {
  opacity: 1;
}
</style>
